<template>
  <div>
    <!-- Grid -->
    <van-grid class="grid" :column-num="2" :gutter="10" square>
      <!--  -->
      <van-grid-item v-for="(item,i) in grids[grid]" v-lazy="item.img" :key="i">
        <van-image :src="item.img" @click="pick(i)"/>
        <p>{{item.text}}</p>
        <img v-show="isShow[i]" src="../assets/pick.png" alt="">
      </van-grid-item>
    </van-grid>
  </div>
</template>

<script>
export default {
  props:[
    "grid",
  ],
  data(){
    return{
      isShow:[],
      grids:[
        [{img:"./static/images/dbai.jpg",text:""},{img:"./static/images/lswjs.jpg",text:""},{img:"./static/images/xa.jpg",text:""},{img:"./static/images/xg.jpg",text:""},{img:"./static/images/tw.jpg",text:""},{img:"./static/images/jd.jpg",text:""}],
        [{img:"./static/images/bj.jpg",text:""},{img:"./static/imgs/278660070981014132.jpg",text:""},{img:"./static/images/xa.jpg",text:""},{img:"./static/imgs/3868824295069646760.jpg",text:""},{img:"./static/images/sy.jpg",text:""},{img:"./static/imgs/3971655992166004676.jpg",text:""},{img:"./static/imgs/2809664543543009291.jpg",text:""},{img:"./static/imgs/0586421334080117600.jpg",text:""}],
        [{img:"./static/images/dbai.jpg",text:""},{img:"./static/images/medf.jpg",text:""},{img:"./static/images/ctd.jpg",text:""},{img:"./static/images/bld.jpg",text:""},{img:"./static/images/mg.jpg",text:""},{img:"./static/images/xjp.jpg",text:""},{img:"./static/images/bhd.jpg",text:""},{img:"./static/images/mgw.jpg",text:""},{img:"./static/images/nl.jpg",text:""},{img:"./static/images/jd.jpg",text:""},{img:"./static/images/dban.jpg",text:""},{img:"./static/images/bty.jpg",text:""},{img:"./static/images/qm.jpg",text:""},{img:"./static/images/ylsl.jpg",text:""}],
        [{img:"./static/images/ctd.jpg",text:""},{img:"./static/images/mlqs.jpg",text:""},{img:"./static/images/fj.jpg",text:""},{img:"./static/images/medf.jpg",text:""},{img:"./static/images/xxld.jpg",text:""},{img:"./static/images/bld.jpg",text:""}],
        [{img:"./static/images/bj.jpg",text:""}],
        [{img:"./static/images/ld.jpg",text:""},{img:"./static/images/ytlk.jpg",text:""},{img:"./static/images/bl.jpg",text:""},{img:"./static/images/ns.jpg",text:""},{img:"./static/images/lnw.jpg",text:""},{img:"./static/images/yd.jpg",text:""},{img:"./static/images/msk.jpg",text:""},{img:"./static/images/ml.jpg",text:""},{img:"./static/images/xxld.jpg",text:""}],
        [{img:"./static/images/xg.jpg",text:""},{img:"./static/images/sdyg.jpg",text:""},{img:"./static/images/mg.jpg",text:""},{img:"./static/images/lj.jpg",text:""},{img:"./static/images/jd.jpg",text:""},{img:"./static/images/bhd.jpg",text:""},{img:"./static/images/msk.jpg",text:""}],
        [{img:"./static/imgs/278660070981014132.jpg",text:""}],
        [{img:"./static/images/mlqs.jpg",text:""},{img:"./static/images/asw.jpg",text:""},{img:"./static/images/dlbl.jpg",text:""},{img:"./static/images/tns.jpg",text:""},{img:"./static/imgs/269446560370519451.jpg",text:""},{img:"./static/imgs/330316305195467557.jpg",text:""},{img:"./static/imgs/296206474426173407.jpg",text:""}],
        [{img:"./static/images/hhz.jpg",text:""},{img:"./static/images/pl.jpg",text:""},{img:"./static/images/lnw.jpg",text:""},{img:"./static/images/kes.jpg",text:""},{img:"./static/images/sdyg.jpg",text:""},{img:"./static/images/ytlk.jpg",text:""}],
        [{img:"./static/imgs/2502111287285243706.jpg",text:""},{img:"./static/imgs/3868824295069646760.jpg",text:""},{img:"./static/imgs/1999181034564276540.jpg",text:""},{img:"./static/imgs/0586421334080117600.jpg",text:""},{img:"./static/imgs/5463456662534195278.jpg",text:""},{img:"./static/imgs/0430201023916284143.jpg",text:""}],
        [{img:"./static/images/sdyg.jpg",text:""},{img:"./static/imgs/1152342764181140.jpg",text:""},{img:"./static/imgs/4108303248689745.jpg",text:""},{img:"./static/imgs/10315126052099722.jpg",text:""},{img:"./static/imgs/146762909668142235.jpg",text:""},{img:"./static/images/lswjs.jpg",text:""}],
        [{img:"./static/images/sy.jpg",text:""},{img:"./static/images/nl.jpg",text:""},{img:"./static/images/qm.jpg",text:""},{img:"./static/images/bty.jpg",text:""},{img:"./static/images/ld.jpg",text:""},{img:"./static/images/xjp.jpg",text:""}],
        [],
        [{img:"./static/imgs/167380009716277962.jpg",text:""},{img:"./static/imgs/190650498358987562.jpg",text:""},{img:"./static/imgs/212223963141011371.jpg",text:""}],
        [{img:"./static/images/ns.jpg",text:""},{img:"./static/images/mgw.jpg",text:""},{img:"./static/images/yd.jpg",text:""},{img:"./static/images/bl.jpg",text:""},{img:"./static/images/ml.jpg",text:""},{img:"./static/images/ylsl.jpg",text:""}],
        [],
        [{img:"./static/imgs/231404942662952035.jpg",text:""},{img:"./static/images/fj.jpg",text:""},{img:"./static/images/pl.jpg",text:""},{img:"./static/images/hhz.jpg",text:""},{img:"./static/images/kes.jpg",text:""},{img:"./static/imgs/252962346591486751.jpg",text:""}]
      ]
    }
  },
  methods:{
    pick(i){
      if(this.isShow[i] == false){
        this.isShow.splice(i,1,true);
      }else{
        this.isShow.splice(i,1,false);
      }
    }
  },
  created() {
    for(var i = 0 ; i < this.grids[this.grid].length; i++){
      this.isShow[i] = false;
    }
  },
}
</script>

<style scoped>
  .grid .van-image{
    overflow: hidden;
    border-radius: 10%;
  }
  .grid .van-image__img{
    width: 107px;
  }
  .grid .van-grid-item__content {
    padding: 8px;
  }
  .grid [class*=van-hairline]::after{
    border: none;
  }
  .grid p{
    margin: -30px 0px 0px 0px;
    color: #fff;
    background-color:rgba(136, 135, 143, 0.25); 
    border-radius: 40%;
    font-weight: 600;
    z-index: 1;
    font-size: 15px;
    position: absolute;
  }
  .grid img{
    position: absolute;
    z-index: 1;
    margin: 25px 0px 0px 30px;
  }
  .grid .img{
    display: none;
  }
</style>



