<template>
  <!-- TreeSelect -->
  <van-tree-select class="treeSelect"
  height="135vw"
  :items="items[id]" 
  :active-id.sync="activeIds"
  :main-active-index.sync="activeIndex">
    <template slot="content" >
      <!-- id --> 
      <grid v-if="activeIndex === 0" :grid="id"></grid>
      <grid v-if="activeIndex === 1" :grid="id+3"></grid>
      <grid v-if="activeIndex === 2" :grid="id+6"></grid>
      <grid v-if="activeIndex === 3" :grid="id+9"></grid>
      <grid v-if="activeIndex === 4" :grid="id+12"></grid>
      <grid v-if="activeIndex === 5" :grid="id+15"></grid>
    </template>
  </van-tree-select>
</template>

<script>
import grid from "@/components/grid";
export default {
  components:{
    grid
  },
  props:[
    "id"
  ],
  data(){
    return {
      activeIndex: 0,
      items: [
        [{ text: ' / 6' },{ text: ' / 6' },{ text: ' / 7' },{ text: ' / 6' },{ text: ' / 6' },{ text: ' / 6' },],
        [{ text: ' / 8' }, { text: ' / 1' },{ text: ' / 1' },{ text: ' / 6' },],
        [{ text: ' / 14' }, { text: ' / 9' },{ text: ' / 6' },{ text: ' / 6' },{ text: ' / 3' },{ text: ' / 6' },]
      ],
      activeIds: [[],[],[]],
    }
  },
}
</script>

<style scoped>
  .treeSelect .van-sidebar-item--select{
    border-color: transparent;
    color: #4bcedd;
  }
</style>



