<template>
  <div>
     <div class="title">
       <router-link to="/my">
          <van-icon name="arrow-left" color="#000" size="17px" />
       </router-link>
          <h4 style="font-weight: normal;"></h4>
          <router-link to="/my">
            <span @click="save_info" style="color:#000;"></span>
          </router-link>
     </div>
     <div class="tabout" style="margin-bottom: 8px;padding:6px 16px">
          <ol type="text"> </ol>
          <ol style="display:flex;align-items: center;">
            <van-uploader :after-read="onRead" accept="image/*">
              <img :src="$store.getters.userpic" style="width:55px;border-radius:50%;" ref="goodsImg"/>
            </van-uploader>
            <van-icon name="arrow" style="color:#444;size:20px" />
          </ol>
     </div>
     <div class="tabout"> 
          <ol class="sign"><input class="signtext" v-model="nick" type="text" placeholder=""></ol>
     </div>
     <div class="tabout"> 
        <ol class="sign"><input class="signtext" v-model="uname" type="text" placeholder=""></ol>
     </div>
     <van-cell is-link @click="showPopup">   
         <ol style="float:right;color:#555;" >{{value}}</ol>
     </van-cell>
     <van-popup position="bottom" style="height:170px" v-model="show1">
        <div  class="sex-box" style="font-size:16px;padding-left:10px">
          <p> </p> 
          <van-icon @click="chacha" name="cross" size="20px"/>
        </div>
        <div @click="sexvalue" class="sex-box"><p></p></div>
        <div @click="sexvalue1" class="sex-box"><p></p></div>
        <div @click="sexvalue2" class="sex-box" style="border:none"><p></p></div>
     </van-popup>
     <van-cell is-link @click="showPopup1"><ol class="choice">{{cty_value}}</ol></van-cell>
     <van-popup position="bottom" v-model="show2">
        <van-area  :area-list="areaList" @confirm="myresult"/>
        
     </van-popup>
     <div class="tabout">
      <ol class="sign"><input class="signtext" v-model="s" type="text" placeholder="">
      </ol>
     </div>
     <!--  -->
     <van-popup position="bottom" v-model="show">
       
     </van-popup> 
  </div>
</template>

<script>
import areaList from "../assets/js/area.js";
import { Toast } from "vant";
import qs from "qs"
export default {
    name: "Address",
  data() {
    return {
        cty_value:"",//
        areaList,
      searchResult: [],
      uid:"",
      pic:"",
      nick:"",//
      uname:"",//
      p:"",
      u_n:"",
      g:"",
    //   a:"",
      s:"",//
      files: {
        name: "",
        type: ""
      },
      headerImage: null,
      picValue: null,
      upImgUrl:null,
      show: false,
      show1: false,
      value:"",//
      show2: false,
      list:["","","",""],
    };
  },
  
  methods: {
    onRead(file) {
		       console.log(file);
		       //
		       this.$refs.goodsImg.src = file.content;
		   },
      // :
      myresult(e){
        this.cty_value = e[0].name+"-"+e[1].name+"-"+e[2].name
          console.log(this.cty_value) ;
          return{}
      },
    showPopup() {
      this.show1 = true;
    },
    showPopup1() {
      this.show2 = true;
    },
    showPopup2() {
      this.show = true;
    },
    showPopup3() {
      this.show = true;
    },
    chacha(){
      this.show1=false;
    },
    sexvalue(){
          this.value = this.list[1],
        this.chacha() 
    },
     sexvalue1(){
      this.value = this.list[2]
      this.chacha()
    }, 
    sexvalue2(){
      this.value = this.list[3]
      this.chacha()
    },
    // 
    save_info(){
        var url = "user/personal";
        console.log(this);  
        this.axios
        .post(
            url,
            qs.stringify({
                // uid:this.uid,
                // pic:this.pic,
                // un: this.un,
                // p:this.p,
                // u_n:this.u_n,
                // g:this.g,
                // // a:this.a,
                // s:this.s,
                nick:encodeURIComponent(this.nick),
                user_name:encodeURIComponent(this.uname),
                gender:this.value,
                cty_value:encodeURIComponent(this.cty_value),
                sign:encodeURIComponent(this.s)
            })
        )

        .then(res=>{console.log(res)})
    },
    
    
  }
};
</script>

<style scoped>
  .title{
    padding: 15px 8px;
    font-size: 16px;
    margin-bottom: 8px;
    align-items: center;
    line-height: 1.8;
    display: flex;
    justify-content: space-between;
    background-color: #fff;
  }
  .van-cell__value{
   display: flex;
    justify-content: space-between;
  }
  .choice,.sign{
    color:#555;
  }
  .tabout{
    align-items: center;
    padding: 10px 16px;
    font-size: 14px;
    line-height: 24px;
    display: flex;
    border-bottom: 1px solid#eeeeee91;
    background-color: #fff;
    justify-content: space-between;
    }
    .signtext{
    height: auto;
    width:260px;
    padding-right:20px;
    text-align: end;
    }
    .signpic{
    background-color:#eee; 
    width:60px;height:60px; 
    border-radius: 50%;
    }
    .sex-box{
      display: flex;
      justify-content: space-between;
      padding: 10px 18px;
      font-size: 14px;
      color: black;
      border-bottom: 1px solid #ddd;
    }
    
</style>



