<template>
  <div class="justgo">
    <!-- NavBar -->
    <div>
      <van-nav-bar
        title=""
        left-text=""
        left-arrow
        @click-left="fanHuiHome"
      />
    </div>
    <!-- Tab -->
    <div>
      <van-tabs line-height="5px" color="#52e0f05e" v-model="activeName" class="container">
        <van-tab title="" name="tj">
          <!-- treeSelect -->
          <tree-select :id="0"></tree-select>
        </van-tab>
        <van-tab title="" name="jn">
          <!-- treeSelect -->
          <tree-select :id="1"></tree-select>
        </van-tab>
        <van-tab title="" name="jw">
          <!-- treeSelect -->
          <tree-select :id="2"></tree-select>
        </van-tab>
      </van-tabs>
    </div>
    <!--  -->
    <div class="next">
      <!-- Tag -->
      <div class="check">
        ...
      </div>
      <router-link to="/login"></router-link>
    </div>
  </div>
</template>

<script>
import treeSelect from "@/components/treeSelect";

export default {
  components:{
    treeSelect
  },
  data(){
    return {
      activeName: 'tj',
    };
  },
  methods: {
    fanHuiHome() {
      this.$router.push("/home");
    },

  }
}
</script>

<style scoped>
  .justgo{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  .justgo > .next{
    width: 100%;
    height: 90px;
    position: fixed;
    bottom: 0px;
  }
  /*  */
  .justgo > .next a{
    color: #4bcedd;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    display: block;
    width: 100%;
    height: 50px;
    text-align: center;
    line-height: 50px;
    font-size: 15px;
    background-color: #fff;
  }
  /*  */
  .justgo .check{
    height: 40px;
    line-height: 40px;
    border-top: 1px solid #ddd;
    font-size: 15px;
    color: #ccc;
    background-color: #fff;
    padding-left: 10px;
  }
  .justgo .van-nav-bar .van-icon {
    color: inherit;
  }
  .justgo .container{
    height: 100%;
  }
</style>



